
import { Component, Vue } from 'vue-property-decorator';

@Component
export default class LoginPage extends Vue {
  userName = "";
  password = "";
  error = false;

  onLoginClick() {
    if(this.userName == 'demo' && this.password == 'qwerty') {
      this.error = false;
      this.$router.push('/main');
    } 
    else {
      this.error = true;
    }
  }
  
}
