
import { Component, Vue } from 'vue-property-decorator';

interface DevicesModel {
  Id: string,
  Name: string,
  IsOn: boolean
}

@Component
export default class LightsData extends Vue {
  devices: DevicesModel[] = [
    {
      Id: 'AIR011',
      Name: "Salon",
      IsOn: true
    },
    {
      Id: 'AIR012',
      Name: 'Sypialnia',
      IsOn: false
    },   
    {
      Id: 'WIN01',
      Name: "Taras",
      IsOn: true
    },
    {
      Id: 'WIN02',
      Name: 'Kuchnia',
      IsOn: false
    }
  ];
  display = false;
  newId = '';
  newName = '';
  onDialogCancelClick() {
    this.display = false;
  }
  onDialogAddClick() {
    if(this.newName.trim() == '') return;
    this.devices.push({
      Id: this.newId,
      Name: this.newName,
      IsOn: false
    });
    this.newId = '';
    this.newName = '';
    this.display = false;
  }
  onAddClick() {
    this.display = true;
  }
  getStatus(id: string, status: boolean) {
    if(id.startsWith("WIN")) {
      return status ? 'Otwarte' : 'Zamknięte'
    }
    else {
      return status ? 'ON' : 'OFF'
    }
  }

  onTurnOffOnClick(data: DevicesModel) {
      data.IsOn = !data.IsOn;
  }
  onDeleteOnClick(data: DevicesModel) {
      this.devices = this.devices.filter(x => data.Id != x.Id);
  }

}
